import { constants as addressConstants } from '@visa/ui-utils/lib/js/address';
const { validationRegex } = addressConstants;

const validateField = ({
  country,
  field,
  hasMask,
  isRequired,
  maskLength,
  validateEmptyRequiredField = false,
  value
}) => {
  const upperCasedCountryCode = country.toUpperCase();
  const upperCasedFieldName =
    field.toUpperCase().indexOf('LINE') > -1 ? 'LINE' : field.toUpperCase();
  const validator = validationRegex[`${upperCasedCountryCode}_${upperCasedFieldName}_REG_EXP`]
    ? validationRegex[`${upperCasedCountryCode}_${upperCasedFieldName}_REG_EXP`]
    : validationRegex[`${upperCasedFieldName}_REG_EXP`];
  let errorText = '';

  if (validateEmptyRequiredField && isRequired && !value) {
    errorText = `${upperCasedFieldName} is required!`;
  } else if (hasMask && maskLength !== value.toString().length) {
    errorText = `Partial ${upperCasedFieldName} value`;
  } else if (value && !validator.test(value)) {
    errorText = `Invalid ${upperCasedFieldName} field`;
  }
  return errorText;
};

export default validateField;
