import { constants as addressConstants } from '@visa/ui-utils/lib/js/address';

import { POSTALCODE_LOOKUP_WHITELIST } from '../constants';

const { validationRegex } = addressConstants;

const checkIfShouldMakeAddressLookup = ({ country, field, value }) => {
  const isPostalCodeField = field === 'postalCode';
  const isPostalCodeLookupCountry = POSTALCODE_LOOKUP_WHITELIST.includes(country);
  const validator = validationRegex[`${country.toUpperCase()}_POSTALCODE_REG_EXP`];

  return isPostalCodeField && isPostalCodeLookupCountry && validator.test(value);
};

export default checkIfShouldMakeAddressLookup;
