import React, { useState } from 'react';
import { arrayOf, bool, func, number, shape, string } from 'prop-types';
import { constants as addressCommonConstants, utilities as addressCommonUtils } from '@visa/ui-utils/lib/js/address';
// import { GOOGLE_ADDRESS_SUGGESTION_SHORT_NAME_FIELDS } from '../constants';
import FreeformCombobox from '../../combobox-freeform-manual-selection';

const {
  googleAddressSuggestConstants
} = addressCommonConstants;


const {
  googleAddressUtils
} = addressCommonUtils;

let placesService;
let service;
let sessionToken;

/* eslint-disable no-undef */
// the google object will be undefined until instantiated
const googleObject = setInterval(() => {
  if (typeof google !== 'undefined') {
    service = new google.maps.places.AutocompleteService();
    placesService = new google.maps.places.PlacesService(document.createElement('div'));
    sessionToken = new google.maps.places.AutocompleteSessionToken();
    clearInterval(googleObject);
  }
}, 10);
// Ends Google Maps API loading
/* eslint-enable no-undef */

const GoogleAddressSuggestCombobox = ({
  comboboxClass,
  countryCode,
  googleAddressSuggestSetupOptions,
  inputProps,
  onGoogleAddressSuggestSelection,
  onInputBlur,
  onInputChange,
  templateName,
  ...restProps
}) => {
  const {
    // customItemToStringFunc,
    fieldMappingForCountry,
    googleAddressSuggestAPIKey,
    positionOfCommaThatFollowsTriggeringFieldAddressInfo,
    suggestionShortNameArray
  } = googleAddressSuggestSetupOptions;

  const [optionsTemplate, setOptionsTemplate] = useState(null);

  googleAddressUtils.loadGoogleAddressScript(googleAddressSuggestAPIKey);

  const itemToStringFunc = item => {
    let valueToInsert = '';
    if (item) {
      const splitValueBasedOnCommaPosition =
        item.value.split(
          googleAddressSuggestConstants.SHORTENED_SELECTED_ITEM_VALUE_CHAR
        )[positionOfCommaThatFollowsTriggeringFieldAddressInfo];

      const splitValueSplitBySpaces = splitValueBasedOnCommaPosition.split(' ');
      const splitValueSplitBySpacesEndingData = splitValueSplitBySpaces[splitValueSplitBySpaces.length - 1];
      const splitValueSplitBySpacesEndingDataBeginsWithNumber =  Number(splitValueSplitBySpacesEndingData.charAt(0)) > -1;
      const splitValueSplitBySpacesEndingDataEndsWithNumber =
        Number(splitValueSplitBySpacesEndingData.charAt(splitValueSplitBySpacesEndingData.length - 1)) > -1;

      valueToInsert = splitValueBasedOnCommaPosition;

      if (
        splitValueSplitBySpacesEndingDataBeginsWithNumber ||
        splitValueSplitBySpacesEndingDataEndsWithNumber
      ) {
        splitValueSplitBySpaces.splice(-1);
        valueToInsert = splitValueSplitBySpaces.join(' ');
      }
    }

    return valueToInsert;
  };

  const getFieldMappingToUse = () => {
    const fieldMappingToUse = fieldMappingForCountry ?
      fieldMappingForCountry :
      googleAddressUtils.getGoogleAddressSuggestionMapping(templateName);

    return fieldMappingToUse;
  };

  const renderSuggestion = suggestion => <span className="address-suggestion">{suggestion.value}</span>;

  let googlePredictions = [];
  let convertedSuggestionAddressModel = {};

  const mapToField = components => {
    if (!components.length) {
      return;
    }

    const suggestionFieldMapping = getFieldMappingToUse();
    const suggestionShortNameFields = suggestionShortNameArray ?
      suggestionShortNameArray :
      googleAddressSuggestConstants.GOOGLE_ADDRESS_SUGGESTION_SHORT_NAME_FIELDS;

    const suggestionFieldInfo = {
      components,
      mapping: suggestionFieldMapping,
      shortNameFields: suggestionShortNameFields
    };

    convertedSuggestionAddressModel = googleAddressUtils.getConvertedAddress(suggestionFieldInfo);

    typeof onGoogleAddressSuggestSelection === 'function' && onGoogleAddressSuggestSelection(convertedSuggestionAddressModel);
  };

  const onInputFieldChange = e => {
    const val = e.target.value;
    const len = val.length;

    if (typeof service !== 'undefined' && len) {
      service.getPlacePredictions({
        componentRestrictions: { country: countryCode.toLowerCase() },
        input: val,
        sessionToken
      }, (predictions, status) => {
        /* eslint-disable no-undef */
        if (status !== google.maps.places.PlacesServiceStatus.OK) {
          return;
        }
        /* eslint-enable no-undef */

        googlePredictions = [];

        predictions.forEach(prediction => {
          googlePredictions.push({
            place_id: prediction.place_id,
            value: prediction.description
          });
        });

        setOptionsTemplate({
          itemKey: 'value',
          optionValues: googlePredictions,
          renderOption: renderSuggestion,
          searchIncludeFields: ['value']
        });
      });
    }

    typeof onInputChange === 'function' && onInputChange(e);
  };

  const onSelectionChange = ({ place_id }) => {
    /* eslint-disable no-undef */
    if (place_id) {
      placesService.getDetails({
        fields: ['address_components'],
        placeId: place_id,
        sessionToken
      }, (place, status) => {
        if (status === google.maps.places.PlacesServiceStatus.OK) {
          mapToField(place.address_components);
        }
      });

      sessionToken = new google.maps.places.AutocompleteSessionToken();
      /* eslint-enable no-undef */
    }
  };

  return (
    <FreeformCombobox
      comboboxClass={comboboxClass}
      inputProps={inputProps}
      itemToStringFunc={itemToStringFunc}
      onInputBlur={onInputBlur}
      onInputChange={onInputFieldChange}
      onSelectionChange={onSelectionChange}
      optionsTemplate={optionsTemplate}
      {...restProps}
    />
  );
};

GoogleAddressSuggestCombobox.propTypes = {
  comboboxClass: shape({
    containerClass: string.isRequired
  }).isRequired,
  countryCode: string.isRequired,
  googleAddressSuggestSetupOptions: shape({
    customItemToStringFunc: func,
    enableGoogleAddressSuggest: bool.isRequired,
    fieldMappingForCountry: shape({}),
    googleAddressSuggestAPIKey: string.isRequired,
    googleAddressSuggestCountryCodeExcludedCountries: arrayOf(string),
    onGoogleAddressSuggestSelection: func,
    positionOfCommaThatFollowsTriggeringFieldAddressInfo: number.isRequired,
    suggestionShortNameArray: arrayOf(string),
    triggeringField: string.isRequired
  }),
  inputProps: shape({
    label: string.isRequired,
    name: string.isRequired,
    placeholder: string,
    required: bool
  }).isRequired,
  onGoogleAddressSuggestSelection: func.isRequired,
  onInputBlur: func.isRequired,
  onInputChange: func.isRequired,
  templateName: string.isRequired
};

export default GoogleAddressSuggestCombobox;
