import React, { Fragment, memo, useEffect, useState } from 'react';
import { arrayOf, bool, func, number, shape, string } from 'prop-types';
import classnames from 'classnames';
import { constants as addressCommonConstants, utilities as addressCommonUtils } from '@visa/ui-utils/lib/js/address/';
import { useTextMask } from '@visa/ui-utils/lib/js/text-mask/react';
import Input from '../../input';
import Combobox from '../../combobox';
import FormControl from '../../form-control';
import GoogleAddressSuggestCombobox from './GoogleAddressSuggestCombobox';
import {
  FORM_CONTROL_CLASSNAME
  // SPLIT_FIELD_BASE_CLASSNAME
} from '../constants';

const { addressPropTypes } = addressCommonConstants;
const { getTemplateDefaults, mergeDefaultsOverrides } = addressCommonUtils;

const preventRerender = (prevProps, nextProps) => JSON.stringify(prevProps) === JSON.stringify(nextProps);

const AddressFields = memo(({
  address,
  countryCode,
  googleAddressSuggestSetupOptions,
  onAddressChange,
  // onCountryChange,
  onFieldBlur,
  templateOptions
}) => {
  const [mergedAddress, setMergedAddress] = useState(address);
  const [addressTemplate] = useState(getTemplateDefaults(templateOptions.name, templateOptions.fields));
  let inputMaskingByField = {};

  const {
    enableGoogleAddressSuggest,
    googleAddressSuggestCountryCodeExcludedCountries,
    onGoogleAddressSuggestSelection,
    triggeringField
  } = googleAddressSuggestSetupOptions;

  useEffect(() => {
    setMergedAddress(address);
  }, [address]);

  const handleAddressChange = e => {
    const field = e.target.name;
    const value = e.target.value;
    const fieldObj = { [field]: value };
    const newAddressInfo = { ...mergedAddress, ...fieldObj };
    setMergedAddress(newAddressInfo);
    if (typeof onAddressChange === 'function') {
      onAddressChange(e);
    }
  };

  const handleFieldBlur = e => {
    if (typeof onFieldBlur === 'function') {
      onFieldBlur(e);
    }
  };

  const handleGoogleAddressSuggestSelection = addressObj => {
    const addressMergedWithSuggestion = { ...address, ...addressObj };

    setMergedAddress(addressMergedWithSuggestion);

    typeof onGoogleAddressSuggestSelection === 'function' && onGoogleAddressSuggestSelection(addressObj);
  };

  // const findSelectedItem = (options, selectedItem) => options.find(item => item.value === selectedItem);

  /* eslint-disable react/prop-types */
  const renderGenericCombobox = ({
    additionalHtmlProps,
    comboboxClass,
    comboboxOptionsTemplate,
    defaultItem,
    inputProps,
    key,
    onComboboxSelect
  }) => (
    <Combobox
      comboboxClass={comboboxClass}
      defaultItem={defaultItem}
      inputProps={inputProps}
      key={key}
      onInputBlur={handleFieldBlur}
      onInputChange={handleAddressChange}
      onSelectionChange={onComboboxSelect}
      optionsTemplate={comboboxOptionsTemplate}
      type={'auto'}
      {...additionalHtmlProps}
    />
  );

  const renderGoogleAddressSuggestCombobox = ({
    additionalHtmlProps,
    comboboxClass,
    countryCode,
    inputProps,
    key
  }) => (
    <GoogleAddressSuggestCombobox
      comboboxClass={comboboxClass}
      countryCode={countryCode}
      googleAddressSuggestSetupOptions={googleAddressSuggestSetupOptions}
      inputProps={inputProps}
      key={key}
      onGoogleAddressSuggestSelection={handleGoogleAddressSuggestSelection}
      onInputBlur={handleFieldBlur}
      onInputChange={handleAddressChange}
      templateName={templateOptions.name}
      {...additionalHtmlProps}
    />
  );
  /* eslint-enable react/prop-types */

  const renderFields = template => {
    const defaults = template.defaults;
    const templateAttributes = templateOptions.overrides
      ? mergeDefaultsOverrides(defaults, templateOptions.overrides)
      : defaults;
    const templateFields = templateOptions.fields ? templateOptions.fields : template.fields;
    // const templateFieldsLength = templateFields.length;

    return templateFields.map(field => {
      const fieldAttributes = templateAttributes[field];
      const fieldName = fieldAttributes.name;
      const isForGoogleAddress =
        enableGoogleAddressSuggest &&
        googleAddressSuggestCountryCodeExcludedCountries.indexOf(countryCode) === -1 &&
        fieldName === triggeringField;

      if (typeof fieldAttributes.shouldRenderField !== 'undefined' && !fieldAttributes.shouldRenderField) {
        return null;
      }

      const fieldContent = templateOptions.content[field];

      const hasErrorText = fieldAttributes.additionalHtmlProps && fieldAttributes.additionalHtmlProps.errorText;

      let errorText = hasErrorText ? fieldAttributes.additionalHtmlProps.errorText : '';

      const shouldRemoveErrorState =
        hasErrorText &&
        (
          fieldAttributes.readOnly ||
          fieldAttributes.renderAsCombobox ||
          (!fieldAttributes.required && !address[field])
        );

      errorText = shouldRemoveErrorState ? '' : errorText;

      if (hasErrorText) {
        delete fieldAttributes.additionalHtmlProps.errorText;
      }

      const textMaskSetup = {
        guide: fieldAttributes.shouldShowInputMasking || false,
        mask: fieldAttributes.mask,
        onChange: handleAddressChange,
        upperCaseValue: fieldAttributes.shouldUpperCaseValue || false,
        value: mergedAddress && mergedAddress[field]
      };
      const textMaskProps = fieldAttributes.hasMask && fieldAttributes.mask ? useTextMask(textMaskSetup).getInputProps : {};
      const fieldInputMaskInfoObj = Object.keys(textMaskProps).length > 0 ?
        { [field]: { hasMask: true, onChange: textMaskProps.onChange } } :
        { [field]: { hasMask: false } };
      inputMaskingByField = { ...inputMaskingByField, ...fieldInputMaskInfoObj };

      const comboboxOptions = fieldAttributes.renderAsCombobox ? fieldAttributes.comboboxOptions : null;
      const shouldSplitField = fieldAttributes.shouldSplitField; // eslint-disable-line
      const splitFieldWidth = fieldAttributes.splitFieldWidth || '';
      const formControlClassNames = classnames( FORM_CONTROL_CLASSNAME, {
        [`vds-form-control--${splitFieldWidth}`]: Boolean(splitFieldWidth)
      });

      const hasAddressData = mergedAddress && mergedAddress[field];
      const hasDefaultValue = fieldAttributes && fieldAttributes.defaultValue;
      let defaultValue = '';

      if (hasAddressData) {
        defaultValue = mergedAddress[field];
      } else if (hasDefaultValue) {
        defaultValue = fieldAttributes.defaultValue;
      }
      let valueToRender =
        fieldAttributes && fieldAttributes.shouldUpperCaseValue ? defaultValue.toUpperCase() : defaultValue;

      if (!valueToRender) {
        valueToRender = '';
      }

      if (isForGoogleAddress || comboboxOptions) {
        let renderData;
        const comboboxClass = {
          containerClass: formControlClassNames
        };
        const inputProps = {
          label: fieldContent.label,
          name: fieldName,
          placeholder: fieldContent.placeholder,
          required: fieldAttributes.required,
          ...fieldAttributes.additionalHtmlProps
        };
        const key = fieldContent.label;

        if (comboboxOptions) {
          const comboboxOptionsTemplate = {
            itemKey: 'value',
            optionValues: comboboxOptions.comboboxItems,
            renderOption: comboboxOptions.renderComboboxOption,
            searchIncludesFields: ['shortValue', 'value']
          };

          renderData = {
            comboboxClass,
            comboboxOptionsTemplate,
            defaultItem: comboboxOptions.selectedItem,
            inputProps,
            key,
            onComboboxSelect: comboboxOptions.onComboboxSelect
          };

          return renderGenericCombobox(renderData);
        } else if (isForGoogleAddress) {
          const valueObj = { value: valueToRender };
          const inputPropsWithValue = { ...inputProps, ...valueObj };
          renderData = {
            comboboxClass,
            countryCode,
            inputProps: inputPropsWithValue,
            key
          };

          return renderGoogleAddressSuggestCombobox(renderData);
        }
      }

      return (
        <FormControl
          key={`vds-address-form-control--${fieldAttributes.name}--${countryCode}`}
        >
          <Input
            autoComplete={fieldAttributes.autoComplete}
            errorText={errorText}
            id={fieldAttributes.id}
            key={`${fieldAttributes.name}--${countryCode}`}
            label={fieldContent.label}
            mask={fieldAttributes.hasMask ? fieldAttributes.mask : null}
            maxLength={fieldAttributes.hasMask ? null : fieldAttributes.maxLength}
            name={fieldAttributes.name}
            noValidate
            onBlur={onFieldBlur}
            onChange={fieldAttributes.hasMask ? null : handleAddressChange}
            placeholder={fieldContent.placeholder}
            readOnly={fieldAttributes.readOnly}
            required={fieldAttributes.required}
            showErrorText
            value={valueToRender}
            {...fieldAttributes.additionalHtmlProps}
            {...textMaskProps}
          />
        </FormControl>
      );
    });
  };

  return <Fragment>{renderFields(addressTemplate)}</Fragment>;
}, preventRerender);

AddressFields.propTypes = {
  address: addressPropTypes.ADDRESS_MODEL_PROP_TYPE,
  countryCode: string,
  googleAddressSuggestSetupOptions: shape({
    customItemToStringFunc: func,
    enableGoogleAddressSuggest: bool.isRequired,
    fieldMappingForCountry: shape({}),
    googleAddressSuggestAPIKey: string,
    googleAddressSuggestCountryCodeExcludedCountries: arrayOf(string),
    onGoogleAddressSuggestSelection: func,
    positionOfCommaThatFollowsTriggeringFieldAddressInfo: number,
    triggeringField: string
  }).isRequired,
  onAddressChange: func,
  // onCountryChange: func,
  onFieldBlur: func,
  templateOptions: shape({
    content: addressPropTypes.CONTENT_PROP_TYPE.isRequired,
    fields: arrayOf(string),
    name: string.isRequired,
    // Please refer to SCHEMA.md in the address utilities for usage of overrides!
    overrides: shape({
      additionalLocation: addressPropTypes.ADDRESS_FIELD_OVERRIDES_PROP_TYPE,
      city: addressPropTypes.ADDRESS_FIELD_OVERRIDES_PROP_TYPE,
      countryCode: addressPropTypes.ADDRESS_FIELD_OVERRIDES_PROP_TYPE,
      county: addressPropTypes.ADDRESS_FIELD_OVERRIDES_PROP_TYPE,
      line1: addressPropTypes.ADDRESS_FIELD_OVERRIDES_PROP_TYPE,
      line2: addressPropTypes.ADDRESS_FIELD_OVERRIDES_PROP_TYPE,
      line3: addressPropTypes.ADDRESS_FIELD_OVERRIDES_PROP_TYPE,
      neighborhood: addressPropTypes.ADDRESS_FIELD_OVERRIDES_PROP_TYPE,
      pointOfReference: addressPropTypes.ADDRESS_FIELD_OVERRIDES_PROP_TYPE,
      postalCode: addressPropTypes.ADDRESS_FIELD_OVERRIDES_PROP_TYPE,
      stateProvinceCode: addressPropTypes.ADDRESS_FIELD_OVERRIDES_PROP_TYPE,
      streetName: addressPropTypes.ADDRESS_FIELD_OVERRIDES_PROP_TYPE,
      streetNumber: addressPropTypes.ADDRESS_FIELD_OVERRIDES_PROP_TYPE
    })
  }).isRequired
};

AddressFields.displayName = 'AddressFields';

export default AddressFields;
