/* eslint-disable require-jsdoc */
/* eslint-disable dot-notation */
/* eslint-disable no-alert */
/* eslint-disable no-console */
/* eslint-disable no-eval */
/* eslint-disable no-negated-condition */
/* eslint-disable prefer-const */
/* eslint-disable no-unused-vars */
/* eslint-disable no-use-before-define */
/* eslint-disable react/jsx-indent */
/* eslint-disable react/sort-comp */
/* eslint-disable react/prop-types */
/* eslint-disable sort-keys */
/* eslint-disable jsx-a11y/label-has-for */
/* eslint-disable spellcheck/spell-checker */

import React, { useEffect, useState } from 'react';
import { constants as addressConstants } from '@visa/ui-utils/lib/js/address';
import Address from '../../address';
import Col from '../../col';
import Grid from '../../grid';
import List from '../../list';
import Row from '../../row';
import * as exampleConstants from './constants';
import {
  checkIfShouldMakeAddressLookup,
  getAddressDataFromPostalCode,
  getLanguageFromLocale,
  getOverrides,
  validateField
} from './utilities';

const { enums, googleAddressSuggestConstants } = addressConstants;

const AddressExample = () => {
  const handleGoogleAddressSuggestSelection = data => {
    console.log('parent handleGoogleAddressSuggestSelection :::: ', data);
  };

  const GOOGLE_ADDRESS_SUGGEST_SETUP_OPTIONS = {
    enableGoogleAddressSuggest: false,
    googleAddressSuggestAPIKey: '',
    googleAddressSuggestCountryCodeExcludedCountries:
      googleAddressSuggestConstants.GOOGLE_ADDRESS_SUGGEST_DISABLED_COUNTRY_CODES,
    onGoogleAddressSuggestSelection: handleGoogleAddressSuggestSelection,
    positionOfCommaThatFollowsTriggeringFieldAddressInfo: 0,
    triggeringField: 'line1'
  };

  const [addressInfo, setAddressInfo] = useState({ countryCode: 'US' });
  const [allAddressLookupFieldsDisplayed, setAllAddressLookupFieldsDisplayed] = useState(false);
  const [brazilTemplate, setBrazilTemplate] = useState('custom');
  const [countryCode, setCountryCode] = useState('US');
  const [prevCountryCode, setPrevCountryCode] = useState('US');
  const [errors, setErrors] = useState({});
  const [googleAddressSuggestSetupOptions, setGoogleAddressSuggestSetupOptions] = useState(
    GOOGLE_ADDRESS_SUGGEST_SETUP_OPTIONS
  );
  const [locale, setLocale] = useState('en-US');
  const [language, setLanguage] = useState('EN');
  const [languageDirection, setLanguageDirection] = useState('ltr');
  const [mexicoTemplate, setMexicoTemplate] = useState('custom');
  const [selectedIrelandCounty, setSelectedIrelandCounty] = useState(enums['IRELAND_COUNTIES'][0]);

  const getActiveFields = () => document.querySelectorAll('.vds-form-control input[type="text"]');

  useEffect(() => {
    if (countryCode !== prevCountryCode) {
      /**
        * Set prevCountryCode to countryCode.
        * This ensures this effect is executed only onCountryChange
        and not on all re-renders.
        */
      setPrevCountryCode(countryCode);

      const countryObj = { countryCode };
      const address = { ...addressInfo, ...countryObj };
      let errorsObj = errors;
      let fieldErrorText = '';
      let fieldErrorObj = {};
      const formFields = getActiveFields();

      formFields.forEach(field => {
        const fieldName = field.name;
        const isRequired = field.required;
        const hasValue = Boolean(field.value);
        const fieldInfo = {
          country: countryCode,
          field: fieldName,
          isRequired,
          validateEmptyRequiredField: isRequired && errorsObj[fieldName],
          value: field.value
        };
        if (fieldName.toUpperCase() !== 'COUNTRYCODE') {
          fieldErrorText = validateField(fieldInfo);
        }
        fieldErrorObj = { [fieldName]: fieldErrorText };

        errorsObj = { ...errorsObj, ...fieldErrorObj };
      });

      setErrors(errorsObj);
      setAddressInfo(address);
    }
  }, [addressInfo, countryCode, prevCountryCode, errors]);

  const setGoogleAPIKey = () => {
    const input = document.getElementById('google-address-api-key');
    const googleAddressAPIKeyObj = {
      enableGoogleAddressSuggest: Boolean(input.value),
      googleAddressSuggestAPIKey: input.value
    };
    const googleSetupInfo = { ...googleAddressSuggestSetupOptions, ...googleAddressAPIKeyObj };

    setGoogleAddressSuggestSetupOptions(googleSetupInfo);
  };

  const renderGoogleAPIKeyMessage = () => {
    if (googleAddressSuggestSetupOptions.googleAddressSuggestAPIKey) {
      return (
        <span
          style={{
            color: 'green',
            display: 'inline-block',
            fontWeight: 'bold',
            marginLeft: '10px'
          }}
        >
          API Key set in state!
        </span>
      );
    }
    return (
      <span
        style={{ color: '#d50000', display: 'inline-block', fontWeight: 'bold', marginLeft: '10px' }}
      >
        No API Key!
      </span>
    );
  };

  const getGoogleAddressSuggestTriggeringField = countryCode => {
    let triggeringField = 'line1';

    if (googleAddressSuggestConstants.COUNTRIES_WITHOUT_LINE1.includes(countryCode)) {
      triggeringField = 'streetName';
    }

    return triggeringField;
  };

  const findSelectedCountryCodeOption = (items, selectedItem) =>
    items.find(item => item.shortValue === selectedItem);

  const findSelectedStateProvinceCodeOption = (items, selectedItem) =>
    items.find(item => item.value === selectedItem);

  const renderComboboxOption = option => <span>{option.value}</span>;

  const setFieldErrors = fieldInfo => {
    const fieldName = fieldInfo.field;
    const validationObj = {
      country: fieldInfo.country,
      field: fieldName,
      hasMask: fieldInfo.hasMask,
      isRequired: fieldInfo.isRequired,
      maskLength: fieldInfo.maskLength,
      validateEmptyRequiredField: fieldInfo.validateEmptyRequiredField,
      value: fieldInfo.value
    };

    const errorText = validateField(validationObj);
    const fieldErrorObj = { [fieldName]: errorText };
    const errorObj = { ...errors, ...fieldErrorObj };

    setErrors(errorObj);
  };

  const handleLocaleChange = e => {
    const newLocale = e.target.name;
    const newLanguageDirection = newLocale === 'ar-AE' ? 'rtl' : 'ltr';

    setLocale(newLocale);
    setLanguage(getLanguageFromLocale(newLocale).toUpperCase());
    setLanguageDirection(newLanguageDirection);
  };

  const getAddressDataFromPostalCode = fieldInfo => {
    console.log('getAddressDataFromPostalCode index');
    return new Promise(resolve => {
      let result = {};

      try {
        if (fieldInfo.country === 'BR') {
          result = {
            city: 'Natal',
            line2: 'Lagoa Nova',
            postalCode: fieldInfo.value,
            stateProvinceCode: 'RN',
            streetName: 'Rua Coronel Auris Coelho'
          };
        } else if (fieldInfo.country === 'MX') {
          result = {
            postalCode: fieldInfo.value,
            stateProvinceCode: 'Jalisco'
          };
        }

        setAddressInfo({ ...addressInfo, ...result });
        resolve(result);
      } catch (e) {
        resolve(null);
        return;
      }
    });
  };

  const handleAddressChange = e => {
    const field = e.target.name;
    const isRequired = e.target.required;
    const validateEmptyRequiredField = false;
    const value = e.target.value;
    const fieldObj = { [field]: value };
    const fieldInfo = {
      country: countryCode,
      field,
      isRequired,
      validateEmptyRequiredField,
      value
    };
    let newAddressInfo = { ...addressInfo, ...fieldObj };
    const shouldMakeAddressLookup = checkIfShouldMakeAddressLookup(fieldInfo);
    const shouldValidateOnChange = countryCode !== 'CA' && countryCode !== 'US';

    if (shouldMakeAddressLookup) {
      getAddressDataFromPostalCode(fieldInfo);
      setAllAddressLookupFieldsDisplayed(true);
      setBrazilTemplate('brazil');
      setMexicoTemplate('mexico');
    }

    if (shouldValidateOnChange && field.toUpperCase() !== 'COUNTRYCODE') {
      setFieldErrors(fieldInfo);
    }

    if (!shouldMakeAddressLookup && field.toUpperCase() !== 'COUNTRYCODE') {
      setAddressInfo(newAddressInfo);
    }
  };

  const handleFieldBlur = e => {
    const field = e.target.name;
    const hasMask = e.target.getAttribute('mask');
    const isRequired = e.target.required;
    const validateEmptyRequiredField = true;
    const value = e.target.value;
    const fieldInfo = {
      country: countryCode,
      field,
      hasMask,
      isRequired,
      maskLength: hasMask ? e.target.getAttribute('mask').match(/,/g).length + 1 : null,
      validateEmptyRequiredField,
      value
    };
    const shouldValidateOnBlur = countryCode === 'CA' || countryCode === 'US';

    if (shouldValidateOnBlur && field.toUpperCase() !== 'COUNTRYCODE') {
      setFieldErrors(fieldInfo);
    }
  };

  const handleCountryChange = item => {
    if (item) {
      const country = item.shortValue;
      const googleTriggeringFieldObj = {
        googleAddressSuggestTriggeringField: getGoogleAddressSuggestTriggeringField(country)
      };
      const googleSetupInfo = { ...googleAddressSuggestSetupOptions, ...googleTriggeringFieldObj };

      setCountryCode(country);
      setGoogleAddressSuggestSetupOptions(googleSetupInfo);
    }
  };

  const handleIECountyChange = item => {
    if (item) {
      const fieldObj = { stateProvinceCode: item.value };
      const address = { ...addressInfo, ...fieldObj };

      setSelectedIrelandCounty(item.value);
      setAddressInfo(address);
    }
  };

  const checkForEmptyRequiredFields = formFields => {
    let errorsObj = {};
    let fieldErrorObj = {};

    formFields.forEach(field => {
      const fieldName = field.name;
      const isRequired = field.required;
      const value = field.value;
      const upperCasedFieldName =
        fieldName.toUpperCase().indexOf('LINE') > -1 ? 'LINE' : fieldName.toUpperCase();
      fieldErrorObj = { [fieldName]: errors.fieldName };

      if (isRequired && !value) {
        fieldErrorObj = { [fieldName]: `${upperCasedFieldName} is required` };
      }

      errorsObj = { ...errorsObj, ...fieldErrorObj };
    });

    setErrors(errorsObj);
  };

  const formHasErrors = formFields => {
    let hasErrors = false;

    for (let field = 0; field < formFields.length; field++) {
      if (formFields[field].value) {
        continue;
      }
      hasErrors = true;
      break;
    }

    return hasErrors;
  };

  const handleSubmit = e => {
    e.preventDefault();
    e.stopPropagation();
    let errorsObj = errors;
    let fieldErrorText = '';
    let fieldErrorObj = {};
    let invalidField = null;
    const formFields = getActiveFields();

    formFields.forEach(field => {
      const fieldName = field.name;
      const hasMask = field.getAttribute('mask');
      const isRequired = field.required;
      const fieldInfo = {
        country: countryCode,
        field: fieldName,
        hasMask,
        isRequired,
        maskLength: hasMask ? field.getAttribute('mask').match(/,/g).length + 1 : null,
        validateEmptyRequiredField: true,
        value: field.value
      };

      if (fieldName.toUpperCase() !== 'COUNTRYCODE') {
        fieldErrorText = validateField(fieldInfo);
      }

      if (invalidField === null && fieldErrorText !== '') {
        invalidField = field;
      }

      fieldErrorObj = { [fieldName]: fieldErrorText };
      errorsObj = { ...errorsObj, ...fieldErrorObj };
    });

    if(invalidField !== null) {
      invalidField.focus();
    }
    
    setErrors(errorsObj);
  };

  const getTemplateName = () => {
    let templateName = exampleConstants['TEMPLATE_MAP_FULL_EXAMPLE'][countryCode];

    if (countryCode === 'BR') {
      templateName = brazilTemplate;
    } else if (countryCode === 'MX') {
      templateName = mexicoTemplate;
    }

    return templateName;
  };

  const getTemplateFields = () => {
    let templateFields = exampleConstants['CUSTOM_TEMPLATE_FIELDS_FULL_EXAMPLE'][countryCode];
    const lookupFields = {
      BR: allAddressLookupFieldsDisplayed
        ? [
            'countryCode',
            'postalCode',
            'streetName',
            'streetNumber',
            'additionalLocation',
            'line2',
            'line3',
            'city',
            'stateProvinceCode'
          ]
        : ['countryCode', 'postalCode'],
      MX: allAddressLookupFieldsDisplayed
        ? [
            'countryCode',
            'streetName',
            'streetNumber',
            'additionalLocation',
            'postalCode',
            'neighborhood',
            'county',
            'city',
            'stateProvinceCode',
            'pointOfReference'
          ]
        : ['countryCode', 'streetName', 'streetNumber', 'additionalLocation', 'postalCode']
    };

    if (countryCode === 'BR' || countryCode === 'MX') {
      templateFields = lookupFields[countryCode];
    }

    return templateFields;
  };

  const countries = exampleConstants[`COUNTRIES_FULL_EXAMPLE_${language}`];
  const irelandCounties = exampleConstants['IRELAND_COUNTIES_FULL_EXAMPLE'];
  const countryObj = {
    allAddressLookupFieldsDisplayed,
    countries,
    countryCode,
    countryCodeSelectedItem: findSelectedCountryCodeOption(countries, countryCode),
    handleCountryChange,
    handleIECountyChange,
    irelandCounties,
    irelandCountySelectedItem: findSelectedStateProvinceCodeOption(
      irelandCounties,
      selectedIrelandCounty
    ),
    renderComboboxOption
  };

  const errorsObj = errors;

  const overrides = getOverrides(countryObj, errorsObj);

  const templateOptions = {
    content: exampleConstants[`CONTENT_FULL_EXAMPLE_${language}`][countryCode],
    fields: getTemplateFields(),
    name: getTemplateName(),
    overrides
  };

  return (
    <Grid>
      <Row>
        <Col>
          <div style={{ display: 'flex', flexWrap: 'wrap' }} dir={languageDirection} lang={locale}>
            <div style={{ width: '100%' }}>
              <div style={{ border: '2px solid blue', marginBottom: '20px', padding: '5px' }}>
                <h2
                  className="vds-text--subtitle"
                >
                  Want to use Google Address Suggest? Enter your api key in the box below and click the
                  button!
                </h2>
                <label
                  htmlFor="google-address-api-key"
                  style={{
                    color: '#616188',
                    cursor: 'pointer',
                    display: 'block',
                    marginLeft: '3px',
                    marginTop: '6px',
                    transition: '.25s ease'
                  }}
                >
                  API Key
                </label>
                <input
                  type="text"
                  id="google-address-api-key"
                  style={{
                    border: '2px solid #000',
                    display: 'inline-block',
                    marginRight: '5px',
                    padding: '4px',
                    width: '50%'
                  }}
                />
                <button
                  type="button"
                  onClick={setGoogleAPIKey}
                  style={{
                    backgroundColor: '#a6bfff',
                    border: '2px solid #000',
                    cursor: 'pointer',
                    padding: '5px'
                  }}
                >
                  Set API Key
                </button>
                {renderGoogleAPIKeyMessage()}
              </div>
              <form onSubmit={handleSubmit} noValidate>
                <Address
                  address={addressInfo}
                  countryCode={countryCode}
                  googleAddressSuggestSetupOptions={googleAddressSuggestSetupOptions}
                  legend="Address Information"
                  onAddressChange={handleAddressChange}
                  onFieldBlur={handleFieldBlur}
                  templateOptions={templateOptions}
                />
                <button type="submit">Submit the form</button>
              </form>
              <div className="locale-buttons">
                <h2>Buttons for switching locales</h2>
                <button type="button" name="en-US" onClick={handleLocaleChange}>
                  en-US
                </button>
                <button type="button" name="ar-AE" onClick={handleLocaleChange}>
                  ar-AE
                </button>
                <button type="button" name="fr-FR" onClick={handleLocaleChange}>
                  fr-FR
                </button>
                <button type="button" name="uk-UA" onClick={handleLocaleChange}>
                  uk-UA
                </button>
                <button type="button" name="zh-CN" onClick={handleLocaleChange}>
                  zh-CN
                </button>
              </div>
            </div>
            <div style={{ width: '100%' }}>
              <h2>Form State: Address Info:</h2>
              <pre>{JSON.stringify(addressInfo, null, 2)}</pre>
            </div>
            <div style={{ width: '100%' }}>
              <h2>Form State: Errors:</h2>
              <pre>{JSON.stringify(errors, null, 2)}</pre>
            </div>
          </div>
        </Col>
      </Row>
    </Grid>
  );
};

export default AddressExample;
